<template>
  <div>
      <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <el-row>
                <el-col>
                    <el-button type="primary" @click="addCateDialog"></el-button>
                </el-col>
                <!--  -->
                <tree-table :data="cateList" border stripe :columns="columns" :selection-type="false" :expand-type="false" :show-index="true" index-text="#">
                    <!--  -->
                    <template slot="isOk" slot-scope="scope">
                        <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: green;"></i>
                        <i class="el-icon-error" v-else style="color: red;"></i>
                    </template>
                    <!--  -->
                    <template slot="Grade" slot-scope="scope">
                        <el-tag v-if="scope.row.cat_level === 0"></el-tag>
                        <el-tag type="success" v-if="scope.row.cat_level === 1"></el-tag>
                        <el-tag type="warning" v-if="scope.row.cat_level === 2"></el-tag>
                    </template>
                    <!--  -->
                    <template slot="setting" slot-scope="scope">
                        <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
                        <el-button size="mini" type="danger" icon="el-icon-delete" @click="cateDel(scope.row.cat_id)"></el-button>
                    </template>
                </tree-table>
                <!--  -->
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryInfo.pagenum"
                    :page-sizes="[1,3,5,10]"
                    :page-size="queryInfo.pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </el-row>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="cateDialogVisible" width="50%" @close="addCateClose">
            <!--  -->
            <el-form :model="addCate" :rules="addCateRules" ref="addCateRef" >
                <el-form-item label=":" prop="cat_name">
                    <el-input v-model="addCate.cat_name"></el-input>
                </el-form-item>
                <el-form-item label=':' >
                    <!--   options  props-->
                    <el-cascader
                    expand-trigger="hover"
                    v-model="selectData"
                    :options="parentCateList"
                    :props="cateProps"
                    @change="parentCateChange" clearable>
                    </el-cascader>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cateDialogVisible = false"> </el-button>
                <el-button type="primary" @click="ifCateDialog"> </el-button>
            </span>
        </el-dialog>
  </div>
</template>

<script>
export default {
    data () {
        return {
            //  
            queryInfo: {
                // 
                type: 3,
                // 
                pagenum: 1,
                // 5
                pagesize: 5
            },
            // 
            total: 0,
            //  
            cateList: [],
            //table
            columns: [
            {
                // 
                label: '',
                // 
                prop: 'cat_name',
            },
            {
                // 
                label: '',
                //   
                type: 'template',
                // 
                template: 'isOk',
            },
            {
                // 
                label: '',
                type: 'template',
                template: 'Grade'
            },
            {
                // 
                label: '',
                type: 'template',
                template: 'setting'
            }],
            // 
            cateDialogVisible: false,
            // 
            addCate: {
                cat_name: '',
                cat_pid: 0,
                cat_level: 0
            },
            // 
            addCateRules: {
                cat_name: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            // 
            parentCateList: [
            ],
            // 
            cateProps: {
                value: 'cat_id',
                label: 'cat_name',
                children: 'children'
            },
            //    
            selectData: []
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        async getCateList () {
            const { data: res } = await this.$http.get('categories',{
                params: this.queryInfo
            })
            if (res.meta.status !== 200) return this.$message.error('')
            // 
            this.cateList = res.data.result
            //  
            this.total = res.data.total
        },
        // pagesize
        handleSizeChange (newSize) {
            this.queryInfo.pagesize = newSize
            this.getCateList()
        },
        //      newPage
        handleCurrentChange (newPage) {
            // console.log(newPage)
            this.queryInfo.pagenum = newPage
            this.getCateList()
        },
        // 
        addCateDialog () {
            this.cateDialogVisible = true
            this.getParentCateList()
        },
        // 
        async getParentCateList () {
            const { data: res} = await this.$http.get('categories', {
                params: {
                    type: 2,
                }
            })
            if (res.meta.status !== 200) return this.$message.error('')
            // console.log(res.data);
            this.parentCateList = res.data
        },
        //   
        parentCateChange () {
            // console.log(this.selectData);
            // this.selectDataid0

            // 
            if(this.selectData.length > 0) {
                this.addCate.cat_pid = this.selectData[this.selectData.length-1]
                this.addCate.cat_level = this.selectData.length
                // console.log(this.addCate);
                return
            }
            // 
            else {
                this.addCate.cat_pid = 0
                this.addCate.cat_level = 0
            }   
        },
        //   
        addCateClose () {
            this.$refs.addCateRef.resetFields()
            this.selectData = []
            this.addCate.cat_pid = 0
            this.addCate.cat_level = 0
        },
        // 
        ifCateDialog () {
            this.$refs.addCateRef.validate(async valid => {
                if (!valid) return
                const {data: res } = await this.$http.post('categories',this.addCate)
                if (res.meta.status !== 201) return this.$message.error('')
                this.getCateList()
                this.cateDialogVisible = false
                this.$message.success('')
            })

        },
        // 
        async cateDel (id) {
            //   confirmResult
            const confirmResult = await this.$confirm(', ?', 
            '', 
            {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            })
            .catch(err => {return err})
            // console.log(confirmResult);
            //    --cancel
            //    --confirm
            if (confirmResult !== 'confirm') return this.$message.info('')
            const { data: res} = await this.$http.delete(`categories/${id}`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            this.getCateList()
        }
    },
    
}
</script>

<style lang="less" scoped>
    .el-col {
        margin-bottom: 20px;
    }
    .el-cascader {
        width: 100%;
    }
</style>
