<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
            <!--  -->
            <el-alert
            title=""
            type="info"
            center
            show-icon>
            </el-alert>
            <!--  -->
            <!-- activeIndex -0    -->
            <el-steps :space="200" :active="activeIndex -0" finish-status="success" align-center>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>
            <!-- from -->
            <el-form :model="addForm" :rules="addFromRules" ref="addFromRef" label-width="100px" label-position="top">
                <!-- tabs 
                v-model="activeIndex"  
            -->
                <el-tabs :tab-position="tabPosition" v-model="activeIndex" :before-leave="beforeTabLeave" @tab-click="tabClick">
                <el-tab-pane label="" name="0">
                    <el-form-item label="" prop="goods_name">
                        <el-input v-model="addForm.goods_name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goods_price" type="number">
                        <el-input v-model="addForm.goods_price"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goods_weight" type="number">
                        <el-input v-model="addForm.goods_weight"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goods_number" type="number">
                        <el-input v-model="addForm.goods_number"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <!--  -->
                        <el-cascader
                            v-model="addForm.goods_cat"
                            :options="cateList"
                            :props="cateProps"
                            @change="handleChange"></el-cascader>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="" name="1">
                    <el-form-item :label="item.attr_name" v-for="item in manyDataList" :key="item.attr_id">
                        <el-checkbox-group v-model="item.attr_vals">
                            <el-checkbox border :label="d" v-for="(d, i) in item.attr_vals" :key="i"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="" name="2">
                    <el-form-item :label="item.attr_name" v-for="item in onlyDataList" :key="item.attr_id">
                        <el-input v-model="item.attr_vals"></el-input>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="" name="3">
                    <!--  -->
                    <!-- 
                        action : api
                        on-preview : 
                        on-remove :  
                        list-type :  
                        on-success :  
                    -->
                    <el-upload
                        :action="uploadURL"
                        :on-success="handleSuccess"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        list-type="picture"
                        :headers="headerObj">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                </el-tab-pane>
                <el-tab-pane label="" name="4">
                    <!--  -->
                    <quill-editor v-model="addForm.goods_introduce">
                    </quill-editor>
                    <el-button type="primary" class="addBtn" @click="add"></el-button>
                </el-tab-pane>
            </el-tabs>
            </el-form>
            
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="previewVisible" width="50%">
            <img :src="previewPath" alt="" class="previewImg">
        </el-dialog>
    </div>
</template>

<script>
// lodash 
import _ from 'lodash'
export default {
    data () {
        return {
            // 
            activeIndex : '0',
            // tabs
            tabPosition: 'left',
            // 
            addForm: {
                //
                goods_name: '',
                // 
                goods_price: 0,
                // 
                goods_weight: 0,
                // 
                goods_number: 0,
                // 
                goods_cat: [],
                // 
                pics : [],
                // 
                goods_introduce: '',
                // 
                attrs : []
            },
            // 
            addFromRules:{
                goods_name : [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                goods_price: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                goods_weight: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                goods_number: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
            },
            // 
            cateList: [],
            // 
            cateProps: { 
                expandTrigger: 'hover',
                //  
                label: 'cat_name', 
                value: 'cat_id', 
                // 
                children: 'children' 
            },
            // 
            manyDataList : [],
            // 
            onlyDataList : [],
            // api
            uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
            // 
            headerObj : {
                Authorization: window.sessionStorage.getItem('token')
            },
            //   ----
            previewPath: '',
            // 
            previewVisible : false,
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        async getCateList () {
            const { data: res } = await this.$http.get('categories')
            if (res.meta.status !== 200) return this.$message.error('')
            this.cateList = res.data
            // console.log(this.cateList);
        },
        // 
        handleChange () {
            console.log(this.addForm.goods_cat);
            if (this.addForm.goods_cat.length !== 3) {
                this.$message.info('')
                this.addForm.goods_cat = []
                // console.log('ok');
            }
        },
        // tab   
        // activeName tabname  oldActiveName tabname
        beforeTabLeave (activeName,oldActiveName) {
            // console.log(activeName)
            // console.log(oldActiveName)
            if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
                this.$message.error('')
                return false
            }
        },
        async tabClick () {
            // tab
            // console.log(this.activeIndex);
            // this.activeIndex1
            if (this.activeIndex === '1') {
                const { data : res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
                    params: {
                        sel : 'many'
                    }
                })
                if (res.meta.status !== 200) return this.$message.error('')
                res.data.forEach(item => {
                    // 
                    item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
                })
                console.log(res.data)
                this.manyDataList = res.data
            }  
            // this.activeIndex2
            if (this.activeIndex === '2') {
                const { data :res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
                    params: {
                        sel : 'only'
                    }
                })
                if (res.meta.status !== 200) return this.$message.error('')
                console.log(res.data);
                this.onlyDataList = res.data
            }
        },
        //  
        handlePreview (file) {
            // console.log(file);
            // 
            this.previewPath = file.response.data.url
            // 
            this.previewVisible = true
        },
        //   file 
        handleRemove (file) {
            // console.log(file);
            // 1.  
            const picPath = file.response.data.tmp_path
            // .2. pics  picIndex
            const picIndex = this.addForm.pics.findIndex(item => {
                item.pic === picPath
            })
            // console.log(picIndex);
            // 3.  splice     
            this.addForm.pics.splice(picIndex, 1)
            console.log(this.addForm);
        },
        // 
        // response    --
        handleSuccess (response) {
            // console.log(response);
            // 1. 
            const picInfo = {pic : response.data.tmp_path}
            // 2.  push  pics
            this.addForm.pics.push(picInfo)
            console.log(this.addForm);
        },
        // 
        add () {
            // console.log(this.addForm);
            // 
            this.$refs.addFromRef.validate(async valid => {
                if (!valid) return this.$message.error('')
                // 
                //  
                // addForm
                const form = _.cloneDeep(this.addForm)
                form.goods_cat = form.goods_cat.join(',')
                // 
                this.manyDataList.forEach(item => {
                    const newManyInfo = {
                        attr_id: item.attr_id,
                        attr_value: item.attr_vals.join(' ')
                        }
                    this.addForm.attrs.push(newManyInfo)
                })
                this.onlyDataList.forEach(item => {
                    const newOnlyInfo = {
                        attr_id: item.attr_id,
                        attr_value: item.attr_vals
                    }
                    this.addForm.attrs.push(newOnlyInfo)
                })
                form.attrs = this.addForm.attrs
                console.log(form);
                // 
                const { data : res } = await this.$http.post('goods', form)
                if (res.meta.status !== 201) return this.$message.error('')
                this.$message.success('')
                // goods
                this.$router.push('/goods')
            })
        }
    },
    computed: {
        cateId() {
            if (this.addForm.goods_cat.length === 3) {
                return this.addForm.goods_cat[2]
            }
            return null
        }
    },
    
}
</script>

<style lang="less" scope>
    .el-checkbox {
        margin: 0 5px !important;
    }
    .previewImg {
        width: 100%;
    }
    .addBtn {
        margin-top: 15px !important;
    }
</style>