<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-alert
            title=""
            type="warning" show-icon>
            </el-alert>
            <!--  -->
            <el-row class="select_opt">
                <el-col>
                    <span></span>
                    <!--  -->
                    <!--options
                        v-model
                        props-->
                    <el-cascader
                    expand-trigger="hover"
                    v-model="selectData"
                    :options="cateList"
                    :props="cateProps"
                    @change="handleChange"
                    clearable>
                    </el-cascader>
                </el-col>
            </el-row>
            <!-- tab  -->
            <el-tabs v-model="activeName" @tab-click="handleTabsClick">
                <!--  -->
                <el-tab-pane label="" name="many">
                    <!--  -->
                    <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addManyAttrDialog"></el-button>
                    <!--  -->
                    <el-table :data="manyTabsData" border stripe>
                        <!--  -->
                        <el-table-column type="expand" width="60px" label="#">
                            <template slot-scope="scope">
                                <el-tag v-for="(item, index) in scope.row.attr_vals" :key="index" type="primary" closable @close="handleClose(index, scope.row)" >{{item}}</el-tag> 
                                <!--  -->
                                <el-input
                                class="input-new-tag"
                                v-if="scope.row.inputVisible"
                                v-model="scope.row.inputValue"
                                ref="saveTagInput"
                                size="small"
                                @keyup.enter.native="handleInputConfirm(scope.row)"
                                @blur="handleInputConfirm(scope.row)"
                                >
                                </el-input>
                                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                            </template>
                        </el-table-column>
                        <!--  -->
                        <el-table-column type="index" label="" width="60px"></el-table-column>
                        <!--  -->
                        <el-table-column label="" prop="attr_name" ></el-table-column>
                        <!--  -->
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-button size="mini" type="primary" icon="el-icon-edit" @click="putParams(scope.row.attr_id)"></el-button><el-button type="danger" size="mini" icon="el-icon-delete" @click="delParams(scope.row.attr_id)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <!--  -->
                <el-tab-pane label="" name="only">
                    <!--  -->
                    <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addOnlyAttrDialog"></el-button>
                    <!--  -->
                    <el-table :data="onlyTabsData" border stripe>
                        <!--  -->
                        <el-table-column type="expand" width="60px" label="#">
                            <template slot-scope="scope">
                                <el-tag v-for="(item, index) in scope.row.attr_vals" :key="index" type="primary" closable @close="handleClose(index, scope.row)" >{{item}}</el-tag> 
                                <!--  -->
                                <el-input
                                class="input-new-tag"
                                v-if="scope.row.inputVisible"
                                v-model="scope.row.inputValue"
                                ref="saveTagInput"
                                size="small"
                                @keyup.enter.native="handleInputConfirm(scope.row)"
                                @blur="handleInputConfirm(scope.row)"
                                >
                                </el-input>
                                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                            </template>
                        </el-table-column>
                        <!--  -->
                        <el-table-column type="index" label="" width="60px"></el-table-column>
                        <!--  -->
                        <el-table-column label="" prop="attr_name" ></el-table-column>
                        <!--  -->
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-button size="mini" type="primary" icon="el-icon-edit" @click="putParams(scope.row.attr_id)"></el-button><el-button type="danger" size="mini" icon="el-icon-delete" @click="delParams(scope.row.attr_id)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <!--  \  -->
        <el-dialog
        :title="'' + titleText"
        :visible.sync="addAttrDialogVisible"
        width="30%"
        @close="addAttrClose">
        <!--  -->
        <!-- 
            addAttr  
            addAttrRules 
            addRolesRef 

        -->
        <el-form :model="addAttr" :rules="addAttrRules" ref="addAttrRef" label-width="90px" >
            <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="addAttr.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addAttrDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addParams"> </el-button>
        </span>
        </el-dialog>

        <!-- \ -->
        <el-dialog
        :title="'' + titleText"
        :visible.sync="putAttrDialogVisible"
        width="30%"
        @close="putAttrClose">
        <!--  -->
        <!-- 
            addAttr  
            addAttrRules 
            addRolesRef 

        -->
        <el-form :model="putAttr" :rules="putAttrRules" ref="putAttrRef" label-width="90px" >
            <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="putAttr.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="putAttrDialogVisible = false"> </el-button>
            <el-button type="primary" @click="okPutParams"> </el-button>
        </span>
        </el-dialog>

    </div>
</template>

<script>
export default {
    data () {
        return {
            // -
            cateList: [],
            // -
            selectData: [],
            // -
            cateProps: {
                value: 'cat_id',
                label: 'cat_name',
                children: 'children'
            },
            // tabs 
            activeName: 'many',
            // 
            manyTabsData: [],
            // 
            onlyTabsData: [],
            // 
            addAttrDialogVisible: false,
            // 
            addAttr: {
                attr_name: ''
            },
            // 
            addAttrRules: {
                attr_name: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            // 
            putAttr: {},
            // 
            putAttrRules: {
                attr_name: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            // 
            putAttrDialogVisible: false,
            
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        // 
        async getCateList () {
            const { data: res } = await this.$http.get('categories')
            if (res.meta.status !== 200) return this.$message.error('')
            // 
            this.cateList = res.data
            // console.log(this.cateList);
        },
        // 
        handleChange () {
            // 
            this.getParamsData()
        },
        // Tab 
        handleTabsClick () {
            // 
            this.getParamsData()
        },
        // 
        async getParamsData () {
            // 3   
            if (this.selectData.length !== 3) {
                this.selectData = []
                this.manyTabsData = [],
                this.onlyTabsData = []
                return
            }
            //  idname
            // console.log(this.selectData);
            const { data: res} = await this.$http.get(`categories/${this.cateId}/attributes`,{
                params: {sel: this.activeName}
            })
            if (res.meta.status !== 200) return this.$message.error('')
            console.log(res.data);
            // res.dataattr_valsres.data
            res.data.forEach(item => {
                // attr_vals  
                item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
                // 
                item.inputVisible = false
                item.inputValue = ''
            })
            console.log(res.data);
            // 
            if (this.activeName === 'many') {
                this.manyTabsData = res.data
            }
            else {
                this.onlyTabsData = res.data
            }
        },
        // 
        addOnlyAttrDialog () {
            this.addAttrDialogVisible = true
        },
        // 
        addManyAttrDialog () {
            this.addAttrDialogVisible = true
        },
        // 
        addAttrClose () {
            this.$refs.addAttrRef.resetFields()
        },
        //  
        addParams () {
            this.$refs.addAttrRef.validate(async valid => {
                if (!valid) return
                const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
                    attr_name: this.addAttr.attr_name,
                    attr_sel: this.activeName,
                })
                if (res.meta.status !== 201) return this.$message.error('' + this.titleText + '')
                this.getParamsData()
                this.addAttrDialogVisible = false
                this.$message.success(''+ this.titleText +'')
            })
        },
        //  
        async putParams (attrId) {
            const { data : res} = await this.$http.get(`categories/${this.cateId}/attributes/${attrId}`, {
                params: {
                    attr_sel: this.activeName,
                }
            })
            if (res.meta.status !== 200) return this.$message.error('')
            // console.log(res.data);
            // 
            this.putAttr = res.data
            // console.log(this.putAttr);
            this.putAttrDialogVisible = true
        },
        // 
        putAttrClose () {
            this.$refs.putAttrRef.resetFields()
        },
        //   
        okPutParams () {
            this.$refs.putAttrRef.validate(async valid => {
                if (!valid) return
                const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.putAttr.attr_id}`, {
                    attr_name: this.putAttr.attr_name,
                    attr_sel: this.activeName,
                })
                if (res.meta.status !== 200) return this.$message.error('' + this.titleText + '')
                this.getParamsData()
                this.putAttrDialogVisible = false
                this.$message.success(''+ this.titleText +'')
            })
        },
        async delParams (id) {
            //   confirmResult
            const confirmResult = await this.$confirm(', ?', 
            '', 
            {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).catch(err => {return err})
            if (confirmResult !== 'confirm') return this.$message.info('')
            const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            this.getParamsData()
        },
        //      Enter 
        handleInputConfirm (row) {
            // 
            if(row.inputValue.trim().length === 0) {
                row.inputVisible = ''
                row.inputVisible = false
                return
            }
            // 
            // console.log(row.inputValue);
            row.attr_vals.push(row.inputValue.trim())
            row.inputValue = '',
            row.inputVisible = false
            // row.attr_vals
            // console.log(row.attr_vals);
            //   
            this.saveAttrVals(row)
        },
        // /-
        async saveAttrVals (row) {
            const { data : res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
                attr_name : row.attr_name,
                attr_sel : row.attr_sel,
                attr_vals : row.attr_vals.join(' ')
            })
            if (res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            console.log(res.data);
        },
        //   
        showInput (attr) {
            attr.inputVisible = true
            // 
            this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus();
                });
        },
        // 
        async handleClose (index, row) {
            // 
            row.attr_vals.splice(index, 1)
            this.saveAttrVals(row)
        }
    },
    // 
    computed: {
        //  truefalse
        isBtnDisabled () {
            if (this.selectData.length !== 3) {
                return true
            }
            return false
        },
        // id
        cateId () {
            //   
            if (this.selectData.length === 3) {
                // id
                return this.selectData[2]
            }
            // null
            return null
        },
        // 
        titleText() {
            if (this.activeName === 'many') {
                return ''
            }
            else {
                return ''
            }
        }
    },
}
</script>

<style lang="less" scoped>
    .select_opt {
        margin: 20px 0;
    }
    
    .el-tag {
        margin: 0 15px 8px 0;
    }
    
    .input-new-tag {
        width: 120px;
    }
</style>
