<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!-- guttercol -->
            <el-row :gutter="25">
                <el-col :span="8">
                    <el-input placeholder="" class="input-with-select" v-model="queryInfo.query" clearable @clear="getGoodsList">
                        <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="goAddPage"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="goodsList" border stripe>
                <!--  -->
                <el-table-column type="index" label="" width="80px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="goods_name" ></el-table-column>
                <!--  -->
                <el-table-column label="()" prop="goods_price" width="120px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="goods_weight" width="120px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="add_time" width="180px">
                    <template slot-scope="scope">
                        {{scope.row.add_time | dateFormat}}
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column label="" width="130px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" ></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" 
                        @click="removeById(scope.row.goods_id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[5,10,12]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
                background>
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
    data () {
        return {
            // 
            queryInfo : {
                // 
                query: '',
                // 
                pagenum : 1,
                // 
                pagesize : 10
            },
            //  
            goodsList: [],
            //  
            total: 0,

        }
    },
    methods: {
        // 
        async getGoodsList () {
            const { data : res} = await this.$http.get(`/goods`, {
                params: this.queryInfo
            })
            if (res.meta.status !== 200) return this.$message.error('')
            console.log(res.data);
            this.goodsList = res.data.goods
            this.total = res.data.total
            // console.log(this.goodsList)
            // console.log(this.total)
        },
        //   newSize
        handleSizeChange (newSize) {
            // console.log(newSize)
            this.queryInfo.pagesize = newSize
            //    pagesize 
            this.getGoodsList()
        },
        //      newPage
        handleCurrentChange (newPage) {
            // console.log(newPage)
            this.queryInfo.pagenum = newPage
            this.getGoodsList()
        },
        // id
        async removeById (id) {
            //   confirmResult
            const confirmResult = await this.$confirm(', ?', 
            '', 
            {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).catch(err => {return err})
            if (confirmResult !== 'confirm') return this.$message.info('')
            const { data: res } = await this.$http.delete(`goods/${id}`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            this.getGoodsList()
        },
        // 
        goAddPage () {
            this.$router.push('/goods/add')
        }
    },
    created() {
        this.getGoodsList()
        console.log(this.goodsList);
    },
}
</script>

<style lang="less" scope>

</style>