<template>
        <div>
        <!--  -->
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!-- guttercol -->
            <el-row :gutter="25">
                <el-col :span="8">
                    <el-input placeholder="" class="input-with-select" v-model="queryInfo.query" clearable @clear="getOrdersList">
                        <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" ></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="ordersList" border stripe>
                <!--  -->
                <el-table-column type="index" label="" width="80px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="order_number" ></el-table-column>
                <!--  -->
                <el-table-column label="" prop="order_price" width="100px"></el-table-column>
                <!--  -->
                <el-table-column label="" width="140px">
                    <template slot-scope="scope">
                        <el-tag type="danger" v-if="scope.row.order_pay === '0'"></el-tag>
                        <el-tag type="success" v-if="scope.row.order_pay === '1'"></el-tag>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column label="" prop="is_send" width="100px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="create_time" width="250px">
                    <template slot-scope="scope">
                        {{scope.row.create_time | dateFormat}}
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column label="" width="160px">
                    <template slot-scope="">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" ></el-button>
                        <!--  -->
                        <el-button type="success" icon="el-icon-setting" size="mini" 
                        ></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[5,10,12]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
                background>
            </el-pagination>
        </el-card>
        </div>
</template>

<script>
export default {
    data () {
        return {
            // 
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 10
            },
            // 
            ordersList: [],
            // 
            total: 0,

        }
    },
    created() {
        this.getOrdersList()
    },
    methods: {
        async getOrdersList () {
            const { data : res} = await this.$http.get(`orders`, {
                params: this.queryInfo
            })
            if (res.meta.status !== 200) return this.$message.error('')
            this.ordersList = res.data.goods
            console.log(this.ordersList);
            // console.log(res.data);
            this.total = res.data.total
            // console.log(this.total);
            // console.log(this.queryInfo);
        },
        //   newSize
        handleSizeChange (newSize) {
            // console.log(newSize)
            this.queryInfo.pagesize = newSize
            //    pagesize 
            this.getOrdersList()
        },
        //      newPage
        handleCurrentChange (newPage) {
            // console.log(newPage)
            this.queryInfo.pagenum = newPage
            this.getOrdersList()
        },
    },
}
</script>

<style>

</style>