<template>
  <div class="login_container">
    <div class="login_box">
      <!-- logo -->
      <div class="logo_box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!--  -->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        label-width="0px"
        class="login_form"
      >
        <!--  -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            prefix-icon="iconfont icon-user"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            prefix-icon="iconfont icon-3702mima"
            type="password"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button :plain="true" type="primary" @click="login"
            ></el-button
          >
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  // 
  data() {
    return {
      // 
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    //  
    login() {
      // validate()promise  
      this.$refs.loginFormRef.validate(async valid => {
        // console.log(valid) 
        // false return
        if (!valid) return
        // ajax   this.loginForm
        // { data : ret } ==promisedataret
        const { data: ret } = await this.$http.post('login', this.loginForm)
        // console.log(ret)
        // 
        if (ret.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        // 1.rettoken sessionStorage 
        //  1.1 
        //  1.2 token token  sessionStorage 
        window.sessionStorage.setItem('token', ret.data.token) // token
        // 2.  /home
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  background-color: pink;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .logo_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    box-shadow: 0 0 10px #ddd;
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #ddd;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 25px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
