<template>
    <el-container class="home-container">
        <!--  -->
        <el-header>
            <div>
                <img src="../assets/dog.png" alt="">
                <span></span>
            </div>
            <el-button type="info" @click="logout"></el-button></el-header>
        <!--  -->
        <el-container>
            <!--  -->
            <el-aside :width="isZhedie ? '64px' : '200px'">
                <!--  -->
                <div class="toggle-btn" @click="zhedie">
                    |||
                </div>
                <el-menu background-color="#333744" text-color="#fff" active-text-color="#409Eff" :unique-opened="true" :collapse="isZhedie" :collapse-transition="false" router :default-active="activePath">
                    <!--  -->
                    <el-submenu :key="item.id" :index="item.id + ''" v-for="item in menulist">
                        <!--  -->
                        <template slot="title">
                            <i :class="iconsObj[item.id]"></i>
                            <span>{{ item.authName }}</span>
                        </template>
                        <!--  -->
                        <el-menu-item :index="'/' + son.path" v-for="son in item.children" :key="son.id" @click="gaoliang('/' + son.path)">
                            <!--  -->
                            <template slot="title">
                            <i class="el-icon-menu"></i>
                            <span>{{ son.authName}}</span>
                            </template>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <!--  -->
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
    
</template>

<script>
export default {
    // 
    data() {
        return {
            // 
            menulist : [],
            // 
            iconsObj : {
                '125': 'iconfont icon-user',
                '103': 'iconfont icon-tijikongjian',
                '101': 'iconfont icon-shangpin',
                '102': 'iconfont icon-danju',
                '145': 'iconfont icon-baobiao'
            },
            // 
            isZhedie : false,
            // 
            activePath: ''
        }
    },
    // 
    created() {
        this.getMenuList()
        this.activePath =  window.sessionStorage.getItem('activePath')
    },
    methods: {
        logout() {
            // token
            window.sessionStorage.clear()
            // 
            this.$router.push('/login')
        },
        //     {data: res} datares 
        async getMenuList() {
            const {data: res} = await this.$http.get('/menus')
            // console.log(res)
            if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
            this.menulist = res.data
        },
        // 
        zhedie() {
            this.isZhedie = !this.isZhedie
        },
        // 
        gaoliang(activepath) {
            window.sessionStorage.setItem('activePath', activepath)
            this.activePath = activepath
        }
    },
}
</script>

<style lang="less" scoped>
    .home-container {
        height: 100%;
    }
    .el-header {
        padding-left: 0;
        background-color: #373d41;
        display: flex;
        // 
        justify-content: space-between;
        align-items: center;
        color: #fff;
        font-size: 20px;
        div {
            display: flex;
            align-items: center;
            margin-left: 15px;
        }
    }
    
    .el-aside {
        background-color: #333744;
        .el-menu {
            border-right: none;
        }
    }

    .el-main {
        background-color: #EAEDF1;
    }

    .iconfont {
        margin-right: 10px;
    }

    .toggle-btn {
        color: #fff;
        background-color: #4A5064;
        line-height: 24px;
        text-align: center;
        font-size: 10px;
        letter-spacing: 0.2em;
        cursor: pointer;
    }
    
</style>

