<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
        <el-table :data="rightsList" border >
            <!--  -->
            <el-table-column type="index" label="" width="60px"></el-table-column>
            <!--  -->
            <el-table-column label="" prop="authName" ></el-table-column>
            <!--  -->
            <el-table-column label="" prop="path"></el-table-column>
            <!--  -->
            <el-table-column label="">
                <!--  -->
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.level === '0'"></el-tag>
                    <el-tag type="success" v-if="scope.row.level === '1'"></el-tag>
                    <el-tag type="warning" v-if="scope.row.level === '2'"></el-tag>
                </template>
            </el-table-column>
        </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
    data () {
        return {
            rightsList: []
        }
    },
    // 
    created() {
        this.getRightsList()
    },
    methods: {
        // 
        async getRightsList () {
            const { data: res } = await this.$http('rights/list')
            if (res.meta.status !==200) return this.$message.error('')
            this.rightsList = res.data
            console.log(this.rightsList);
        }
    },
}
</script>

<style lang="less" scoped>
.el-breadcrumb {
  font-size: 12px;
  margin-bottom: 15px;
}
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
.el-table {
    font-size: 14px;
    }
</style>
