<template>
  <div>
      <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
        <!--  -->
        <el-row>
            <el-col>
                <el-button type="primary" @click="addRolesDialogVisible = true"></el-button>
            </el-col>
        </el-row>
        <!--  -->
        <el-table :data="rolesList" border stripe>
            <!--  -->
            <el-table-column type="expand" width="60px" label="#">
                <template slot-scope="scope">
                    <el-row :class="['bdbotton', index1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, index1) in scope.row.children" :key="item1.id">
                        <!--  -->
                        <el-col :span="5">
                            <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                            <i class="el-icon-caret-right"></i>
                        </el-col>
                        <!--  -->
                        <el-col :span="19">
                            <!-- for -->
                            <el-row v-for="(item2, index2) in item1.children" :key="item2.id" :class="['bdbotton', index2 === 0 ? '' : 'bdtop', 'vcenter']">
                                <el-col :span="6">
                                    <el-tag type="success" closable @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                                    <i class="el-icon-caret-right"></i>
                                </el-col>
                                <el-col :span="10">
                                    <el-tag v-for="item3 in item2.children" :key="item3.id" type="warning" closable @close="removeRightById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                                </el-col>
                            </el-row>
                        </el-col>
                    </el-row>
                </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="" width="60px"></el-table-column>
            <!--  -->
            <el-table-column label="" prop="roleName" ></el-table-column>
            <!--  -->
            <el-table-column label="" prop="roleDesc"></el-table-column>
            <!--  -->
            <el-table-column label="">
                <!--  -->
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button><el-button type="danger" size="mini" icon="el-icon-delete" @click="rolesDel(scope.row.id)"></el-button><el-button type="warning" size="mini" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-card>

        <!--  -->
        <el-dialog
            title=""
            :visible.sync="addRolesDialogVisible"
            width="50%"
            @close="addRolesDialogClose"
            >
            <!--  -->
            <el-form :model="addRoles" :rules="addRolessRules" ref="addRolesRef" label-width="90px" >
                <el-form-item label="" prop="roleName">
                    <el-input v-model="addRoles.roleName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="roleDesc">
                    <el-input v-model="addRoles.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addRolesDialogVisible = false"> </el-button>
                <el-button type="primary" @click="ifAddRoles"> </el-button>  
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="putRolesDialogVisible"
            width="50%"
            @close="putRolesDialogClose"
            >
            <!--  -->
            <el-form :model="putRoles" :rules="putRolessRules" ref="putRolesRef" label-width="90px" >
                <el-form-item label="" prop="roleName">
                    <el-input v-model="putRoles.roleName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="roleDesc">
                    <el-input v-model="putRoles.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="putRolesDialogVisible = false"> </el-button>
                <el-button type="primary" @click="ifPutRoles"> </el-button>  
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="setRightDialogVisible"
            width="50%"
            @close="setRightDialogClose"
            >
            <!-- -- -->
            <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" :default-expand-all="true" :default-checked-keys="defKeys" ref="treeRef"></el-tree>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="setRightDialogVisible = false"> </el-button>
                <el-button type="primary" @click="ifRights"> </el-button>  
            </span>
        </el-dialog>
  </div>
</template>

<script>
export default {
    data () {
        return {
            // 
            rolesList: [],
            // 
            addRolesDialogVisible: false,
            // 
            addRoles: {
                roleName: '',
                roleDesc: ''
            },
            // 
            addRolessRules: {
                roleName: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 2,
                        max: 8,
                        message: '2~8',
                        trigger: 'blur'
                    }
                ],
                roleDesc: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            // 
            putRolesDialogVisible: false,
            // 
            putRoles: {},
            // 
            putRolessRules: {
                roleName: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    
                ],
                roleDesc: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                ]
            },
            // 
            setRightDialogVisible: false,
            // 
            rightsList : [],
            // 
            treeProps: {
                label: 'authName',
                children: 'children'
            },
            // id  id
            defKeys: [],
            // id
            roleId: '',
            

        }
    },
    created() {
        this.getRolesList()
    },
    methods: {
        async getRolesList () {
            const { data: res } = await this.$http.get('roles')
            if (res.meta.status !== 200) return this.$message.error('')
            this.rolesList = res.data
            console.log(this.rolesList);
        },
        addRolesDialogClose () {
            this.$refs.addRolesRef.resetFields()
        },
        // 
        async ifAddRoles () {
            // 
            this.$refs.addRolesRef.validate(async valid => {
                // 
                if(!valid) return
                // 
                const { data: res } = await this.$http.post('roles', this.addRoles)
                if (res.meta.status !== 201) return this.$message.error('')
                this.$message.success('')
                // 
                this.addRolesDialogVisible = false
                // 
                this.getRolesList()
            })
        },
        // 
        async rolesDel (id) {
            //   confirmResult
            const confirmResult = await this.$confirm(', ?', 
            '', 
            {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            })
            .catch(err => {return err})
            // console.log(confirmResult);
            //    --cancel
            //    --confirm
            if (confirmResult !== 'confirm') return this.$message.info('')
            const { data: res} = await this.$http.delete(`roles/${id}`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            this.getRolesList()
        },
        //    y
        putRolesDialogClose () {
            this.$refs.putRolesRef.resetFields()
        },
        // 
        async showEditDialog (id) {
            const { data: res } = await this.$http.get(`roles/${id}`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.putRoles = res.data
            console.log(this.putRoles);
            // 
            this.putRolesDialogVisible = true
        },
        // 
        ifPutRoles () {
            this.$refs.putRolesRef.validate(async valid => {
                if (!valid) return
                // 
                const { data: res } = await this.$http.put(`roles/${this.putRoles.roleId}`,{
                    roleName: this.putRoles.roleName,
                    roleDesc: this.putRoles.roleDesc
                })
                if (res.meta.status !== 200) return this.$message.error('')
                this.putRolesDialogVisible = false
                this.getRolesList()
                this.$message.success('')
            })
        },
        // idtag   role.idID,rightIdid
        async removeRightById (role,rightId) {
            //   confirmResult
            const confirmResult = await this.$confirm(', ?', 
            '', 
            {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).catch(err => {return err})
            if (confirmResult !== 'confirm') return this.$message.info('')
            const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            //  
            // this.getRolesList()
            role.children = res.data
        },
        // 
        async showSetRightDialog (role) {
            this.roleId = role.id
            //
            const { data: res } = await this.$http.get(`rights/tree`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.rightsList = res.data
            // console.log(this.rightsList);
            // 
            console.log(role);
            this.getDefKeys(role, this.defKeys)
            this.setRightDialogVisible = true
        },
        // id
        getDefKeys (node, arr) {
            //   nodechildren
            if (!node.children) {
                // id push arr
                return arr.push(node.id)
            }
            // 
            node.children.forEach(item => {
                this.getDefKeys(item, arr)
            }
            )
        },
        // 
        setRightDialogClose () {
            this.defKeys = []
        },
        //  -- 
        async ifRights () {
            // keysid
            const keys = [
                // id
                ...this.$refs.treeRef.getCheckedKeys(),
                // id
                ...this.$refs.treeRef.getHalfCheckedNodes()
            ]
            // console.log(keys)
            //   keys   
            const idStr = keys.join(',')
            // console.log(idStr);
            //  
            const { data: res} = await this.$http.post(`roles/${this.roleId}/rights`,{
                rids: idStr
            })
            if (res.meta.status !== 200) return this.$message.error('')
            this.setRightDialogVisible = false
            this.getRolesList()
            this.$message.success('')
        },
        
    },
}
</script>

<style lang="less" scoped>
.el-table {
    margin-top: 25px;
}
.el-tag {
    margin: 9px;
}
.bdtop {
    border-top: 1px solid #eee;
}
.bdbotton {
    border-bottom: 1px solid #eee;
}
.vcenter {
    display: flex;
    align-items: center;
}
</style>
