<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!-- row :span="7"  24  Bootstrap    :gutter="20"  col-->
            <el-row :gutter="20">
                <el-col :span="7">
                    <!--  -->
                    <el-input placeholder="" v-model="usersListParams.query" clearable @clear="getUserList">
                        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true"></el-button>
                </el-col>
            </el-row>
            <!--   :data user  propuser-->
            <el-table :data="usersList" border stripe>
                <!--  -->
                <el-table-column type="index" label="" width="80px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="username" width="180px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="email" width="180px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="mobile" width="180px"></el-table-column>
                <!--  -->
                <el-table-column label="" prop="role_name" width="180px"></el-table-column>
                <!--  -->
                <el-table-column label="" width="180px">
                    <template slot-scope="scope">
                        <!--  -->
                        <!-- {{scope.row}} -->
                        <el-switch v-model="scope.row.mg_state" active-color="#13ce66" inactive-color="#ff4949" @change="userState(scope.row)">
                        </el-switch>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column label="" width="200px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="delUsers(scope.row.id)"></el-button>
                        <!--  -->
                        <el-tooltip class="item" effect="dark" content="" placement="right" :enterable="false">
                            <el-button @click="setRole(scope.row)" type="warning" icon="el-icon-setting" size="mini"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="usersListParams.pagenum"
                :page-sizes="[1,2,3]"
                :page-size="usersListParams.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="addDialogVisible"
            width="50%"
            @close="addDialogClose"
            >
            <!--  -->
            <el-form :model="addUsers" :rules="addUsersRules" ref="addUsersRef" label-width="70px" >
                <el-form-item label="" prop="username">
                    <el-input v-model="addUsers.username"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input v-model="addUsers.password"></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input v-model="addUsers.email"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mobile">
                        <el-input v-model="addUsers.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="ifAddUsers"> </el-button>  
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="putDialogVisible"
            width="50%"
            @close="putDialogClose"
            >
            <!--  -->
            <el-form :model="putUsers" :rules="putUsersRules" ref="putUsersRef" label-width="70px" >
                <el-form-item label="">
                    <el-input v-model="putUsers.username" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input v-model="putUsers.email"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mobile">
                    <el-input v-model="putUsers.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="putDialogVisible = false"> </el-button>
                <el-button type="primary" @click="isPutUsers"> </el-button>  
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="setRoleVisible"
            width="50%"
            @close="setRoleDialogClose"
            >
            <div>
                <p>: {{roleInfo.username}}</p>
                <p>: {{roleInfo.role_name}}</p>
                <p>: 
                    <template>
                        <el-select v-model="selectRoleId" placeholder="">
                            <el-option
                            v-for="item in roleList"
                            :key="item.id"
                            :label="item.roleName"
                            :value="item.id">
                            </el-option>
                        </el-select>
                    </template>
                </p>
            </div>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="setRoleVisible = false"> </el-button>
                <el-button type="primary" @click="isSetRole"> </el-button>  
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        // bug
            // 
            // var checkEmail = (rule, value, callback) => {
            //     // 
            //     const regEmail = /^([a-zA-Z0-9_-])+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-])+/

            //     if (regEmail.test(value)) {
            //         // 
            //         return callback()
            //     }
            //     callback(new Error(''))
            // },

            // // 
            // var checkMobile = (rule, value, callback) => {
            //     // 
            //     const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]18[0-9]|14[57])[0-9]{8}$/

            //     if (regMobile.test(value)) {
            //         // 
            //         return callback()
            //     }
            //     callback(new Error(''))
            // }
        return {
            // 
            usersListParams: {
                // 
                query: '',
                // 
                pagenum: 1,
                // 
                pagesize: 3
            },
            // 
            usersList: [],
            total: 0,
            // 
            addDialogVisible: false,
            // 
            putDialogVisible: false,
            // 
            addUsers: {
                username: '',
                password: '',
                email: '',
                mobile: ''
            },
            // 
            addUsersRules: {
                username: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 2,
                        max: 8,
                        message: '2~8',
                        trigger: 'blur'
                    }
                ],
                password: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 6,
                        max: 15,
                        message: '6~15',
                        trigger: 'blur'
                    }
                ],
                email: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    // 
                    // {
                    //     validator: checkEmail,
                    //     trigger: 'blur'
                    // }
                ],
                mobile: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    // 
                    // {
                    //     validator: checkMobile,
                    //     trigger: 'blur'
                    // }
                ]
            },
            // 
            putUsersRules: {
                email: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    // 
                    // {
                    //     validator: checkEmail,
                    //     trigger: 'blur'
                    // }
                ],
                mobile: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                ]
            },
            //  
            putUsers: {},
            // 
            setRoleVisible: false,
            // 
            roleInfo : {},
            // 
            roleList : [],
            // 
            selectRoleId : ''
        }
    },
    // 
    created() {
        this.getUserList()
    },
    methods: {
        async getUserList () {
            // params
            const { data: res} = await this.$http.get('users', {params: this.usersListParams})
            // console.log(res)
            if(res.meta.status !== 200) return this.$message.error('')
            this.usersList = res.data.users
            this.total = res.data.total
            console.log(this.usersList)
        },
        //   newSize
        handleSizeChange (newSize) {
            // console.log(newSize)
            this.usersListParams.pagesize = newSize
            //    pagesize 
            this.getUserList()
        },
        //      newPage
        handleCurrentChange (newPage) {
            // console.log(newPage)
            this.usersListParams.pagenum = newPage
            this.getUserList()
        },
        // switch    
        async userState (users) {
            console.log(users.mg_state);
            // 
            const {data : res} = await this.$http.put(`users/${users.id}/state/${users.mg_state}`)
            if (res.meta.status !== 200) {
                users.mg_state = !users.mg_state
                return this.$message.error('')
            }
            this.$message.success('')
        },
        //    resetFields()
        addDialogClose () {
            this.$refs.addUsersRef.resetFields()
        },
        //  --
        ifAddUsers () {
            // 
            this.$refs.addUsersRef.validate(async valid => {
                if(!valid) return
                // 
                const {data : res} = await this.$http.post('users', this.addUsers)

                if(res.meta.status !== 201) return this.$message.error('')
                this.$message.success('')
                // 
                this.addDialogVisible = false
                // 
                this.getUserList()
            })
        },
        //  id
        async showEditDialog (id) {
            // console.log(id);
            const { data: res} = await this.$http.get(`users/${id}`)
            if (res.meta.status !==200) return this.$message.error('')
            this.putUsers = res.data
            // console.log(this.putUsers);
            this.putDialogVisible = true
        },
        //   
        putDialogClose () {
            this.$refs.putUsersRef.resetFields()
        },
        //  --
        isPutUsers () {
            this.$refs.putUsersRef.validate(async valid => {
                if (!valid) return
                // 
                const { data: res} = await this.$http.put(`users/${this.putUsers.id}`,{
                    email: this.putUsers.email,
                    mobile: this.putUsers.mobile
                })
                if (res.meta.status !== 200) {
                    return this.$http.message.error('')
                }
                else {
                    this.putDialogVisible = false
                    this.getUserList()
                    this.$message.success('')
                }
            })
        },
        // 
        async delUsers (id) {
            //   confirmResult
            const confirmResult = await this.$confirm(', ?', 
            '', 
            {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            })
            .catch(err => {return err})
            // console.log(confirmResult);
            //    --cancel
            //    --confirm
            if (confirmResult !== 'confirm') return this.$message.info('')
            const { data: res} = await this.$http.delete(`users/${id}`)
            if (res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            this.getUserList()
        },
        //  
        async setRole (roleInfo) {
            this.roleInfo = roleInfo
            // 
            const { data : res} = await this.$http.get('roles')
            if (res.meta.status !== 200) return this.$message.error('')
            this.roleList = res.data
            this.setRoleVisible = true
        },
        // 
        setRoleDialogClose () {
            // 
            this.selectRoleId = ''
            this.roleInfo = {}
            this.setRoleVisible = false
        },
        //  
        async isSetRole () {
            if(!this.selectRoleId) {
                return this.$message.error('')
            }
            const { data: res} = await this.$http.put(`users/${this.roleInfo.id}/role`,{
                rid : this.selectRoleId
            })
            if (res.meta.status !== 200) return this.$message.error('')
            this.getUserList()
            this.setRoleVisible = false
            this.$message.success('')
        },
        
    }
}
</script>

<style lang="less" scoped>
</style>